                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑤</span>
                        <span style="font-family:宋体; font-size:12pt">「テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品」とは加盟店が当システムに情報を掲載することによってユーザーに対して販売提供する飲料、食品、その他同種の商品をいう。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑥</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >「加盟店管理画面」とは加盟店の情報及びテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品情報の掲載、当社及びユーザーからの連絡の送受信、その他各種設定を行うページをいう。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑦</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >「アカウント」とは加盟店に対し当社が発行付与する加盟店管理画面、</span>
                        <span style="font-family:宋体; font-size:12pt">及び当該加盟店管理画面にアクセスするために必要な</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">ID</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">パスワードをいう。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑧</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >「利用手数料」とは加盟店が当社に対して支払う手数料をいい、テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の売上金額（税抜）に対して当社が別途定める料率を乗じた金額及びその他手数料をいう。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">3</span>
                        <span style="font-family:宋体; font-size:12pt">条（申込）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店を希望する者（以下「加盟店希望者」という）は、本規約に同意したうえで、当社所定の方法により当社に対し申込を行わなければならない。なお、申込にあたって、加盟店希望者は自身が当システムに加盟するために必要な法令上の手続き（食品営業許可申請等テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の提供に必要なものを指すがこれに限らない）を完了していることを保証するものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店希望者の加盟にあたっては、申込に対する当社の承諾が必要なものとする。以下のいずれかに該当すると当社が判断した場合、当社は、加盟店希望者の申込を拒絶することができるものとする。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">①</span>
                        <span style="font-family:宋体; font-size:12pt">当社に対し、虚偽の情報を提供したとき</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">②</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社所定の審査基準（なお、当社はかかる審査基準を開示する義務を負わない）を満たさないとき</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">③</span>
                        <span style="font-family:宋体; font-size:12pt">その他、当システムをご利用いただくことが適当でないとき</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">4</span>
                        <span style="font-family:宋体; font-size:12pt">条（契約期間）</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">本規約の契約期間は</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">1</span>
                        <span style="font-family:宋体; font-size:12pt">年とし、当社又は加盟店の別段の申し出がない限り、</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">1</span>
                        <span style="font-family:宋体; font-size:12pt">年毎に自動で更新されるものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">5</span>
                        <span style="font-family:宋体; font-size:12pt">条（利用解約）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span style="font-family:宋体; font-size:12pt">加盟店は加盟店及び当システムの利用を停止する日の</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">30</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >日前までに当社に対して解約の申し入れを書面又は電子メールで行うことによって本規約を解約することができるものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は解約の申し入れを行った後、当社が加盟店管理画面を削除し利用を停止するまで本規約によって義務付けられる加盟店の義務を履行しなければならない。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span style="font-family:宋体; font-size:12pt">当社は加盟店から解約の申し入れを受けた日から</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">30</span>
                        <span style="font-family:宋体; font-size:12pt">日以内に加盟店管</span>
                        <span style="font-family:宋体; font-size:12pt">理画面の削除等の手配をするものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">4.</span>
                        <span style="font-family:宋体; font-size:12pt">売上金の支払いは解約の日の属する月の翌月末に支払うものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">6</span>
                        <span style="font-family:宋体; font-size:12pt">条（届出事項）</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">加盟店は、第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">3</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >条の申込に際し、以下の事項をあらかじめ当社に届け出るものとし、届出後に以下の事項に変更がある場合にも同様とする。届出がなかったことによる損害は加盟店の負担とする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span style="font-family:宋体; font-size:12pt">会社名、代表者名及び住所。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店についての責任者（以下「管理責任者」という）の氏名、当社からの連絡を遅滞なく確認できる電子メールアドレス、電話番号その他当社所定の事項。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社から加盟店に対し当システムへの加盟店によって発生した売上金を振込む際に必要な口座情報。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">4.</span>
                        <span style="font-family:宋体; font-size:12pt">その他当社が指定する加盟店の業務に関する事項。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">7</span>
                        <span style="font-family:宋体; font-size:12pt">条（加盟店管理画面の開設）</span>
                        <br />
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、加盟店に対し、加盟店管理画面を開設するとともに、加盟店管理画面にアクセスするために必要となる</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">ID</span>
                        <span style="font-family:宋体; font-size:12pt">及びパスワードを発行する。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">8</span>
                        <span style="font-family:宋体; font-size:12pt">条（加盟店管理画面情報の入稿）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、当社の定める規格に従い、アカウント発行日から合理的期間内に、店舗やテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品についての情報等（以下「コンテンツ」という）を加盟店管理画面上に制作する。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、前項のコンテンツの制作にあたり、次の事項を遵守するものとする。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">①</span>
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">18</span>
                        <span style="font-family:宋体; font-size:12pt">条その他本規約等に反する表示をしないこと</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">②</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >わいせつ、グロテスクその他ユーザー及び一般人が不快感を覚える表示をしないこと</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">③</span>
                        <span style="font-family:宋体; font-size:12pt">テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品等に特定商取引に関する法律が適用されるか否かにかかわらず、同法</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">11</span>
                        <span style="font-family:宋体; font-size:12pt">条及び同法施行規則</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">8</span>
                        <span style="font-family:宋体; font-size:12pt">条に</span>
                        <span style="font-family:宋体; font-size:12pt">より表示を義務づけられた事項について表示すること</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">④</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >前号のほか、以下の事項について表示するとともに、随時最新の内容に更新すること</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">ア</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">.</span>
                        <span style="font-family:宋体; font-size:12pt">加盟店管理画面の担当者の氏名、電話番号及び電子メールアドレス</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">イ</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">.</span>
                        <span style="font-family:宋体; font-size:12pt">営業時間、定休日等</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">ウ</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">.</span>
                        <span style="font-family:宋体; font-size:12pt">テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品等についての問合せ及び苦情は加盟店宛に行うようユーザーに対して案内すること</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">エ</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">.</span>
                        <span style="font-family:宋体; font-size:12pt">当社指定のユーザー店舗評価ポイント画面</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">オ</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">.</span>
                        <span style="font-family:宋体; font-size:12pt">その他当社所定の事項</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、加盟店の制作したコンテンツを当社の裁量においてその内容及び表示を変更することができる。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">9</span>
                        <span style="font-family:宋体; font-size:12pt">条（クレームの処理）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、加盟店が制作したコンテンツ、加盟店の店頭でのサービス及び引き渡したテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品等に関する第三者からの問合せ、クレーム等について、自己の責任と費用負担をもって誠実に対応するものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、加盟店が制作したコンテンツ、店頭でのサービス及び引き渡したテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品等に関して第三者から損害賠償請求、その他提訴を受け、又はそのおそれがある場合、直ちにその旨を当社に通知し、加盟店は自己の責任と費用負担によりこれを解決するものとする。この場合、当社が損害を被ったときは、加盟店はこれを当社に賠償するものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">10</span>
                        <span style="font-family:宋体; font-size:12pt">条（商品価格の設定）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span style="font-family:宋体; font-size:12pt">加盟店が当システムで販売するテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の価格（税込）は、容器その他の雑費を含めて、加盟店が店舗又は当システムと同種もしくは類似のサービスで提供する価格（税込）（以下本条において「店舗等提供価格」という）と同額か、それよりも低い価格で設定しなければならない。加盟店が当システム向けにテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品を開発した場合には、加盟店の店舗又は当システ</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >ムと同種もしくは類似のサービスで提供している同等の商品と同額か、それよりも低い価格に設定するものとする。なお、加盟店は、店舗等提供価格を改定した場合には速やかに当システムにおける販売価格にも反映させるものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >前項の定めに反していると当社が判断した場合、当社は加盟店に対してテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の価格の改定を催告することができる。当該催告に対して加盟店が合理的な理由なく対応を怠った場合、当社は加盟店の取り消し及びその他の処理を行うことができるものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span style="font-family:宋体; font-size:12pt">テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の価格（税抜）が店舗等提供価格を超えるものとしてユーザーから当社に対して申告がなされ、その結果加盟店が第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">1</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >項の定めに反していると当社が判断した場合、当該加盟店は、かかる申告をなしたユーザーがテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品を購入する際に支払った料金の総額（テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品代金及びその注文において発生した一切のサービス料金を含む）を当社に対して支払うものとする。かかる支払いは、第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">15</span>
                        <span style="font-family:宋体; font-size:12pt">条第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">1</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >項に定める当社から加盟店への支払額から差し引く方法による。本項の規定は、当社による前項の処理及び当社から加盟店に対する損害賠償請求を妨げるものではない。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">11</span>
                        <span style="font-family:宋体; font-size:12pt">条（テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品の売買契約の成立）</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">加盟店とユーザーとの間におけるテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の売買契約は、ユーザーが当システム上で行った注文を加盟店が当システム上で承認した後にテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品の調理を開始したときに成立するものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">12</span>
                        <span style="font-family:宋体; font-size:12pt">条（テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品の引渡しの完了）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、前条に基づいてユーザーとの間で売買契約が成立したテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品を、当システム上で指定された時間までに、加盟店管理画面において引渡し場所として定めた店舗に用意するものとする。当システムを通じて注文を行ったユーザーが、加盟店の店舗で</span>
                        <span style="font-family:宋体; font-size:12pt">該当のテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品を受け取ったとき、引渡しが完了するものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span style="font-family:宋体; font-size:12pt">加盟店がテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品を用意したにもかかわらず、指定した引渡しの時間から</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">2</span>
                        <span style="font-family:宋体; font-size:12pt">時間を経過してもユーザーがテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品を取りに来なかった場合、引渡しは完了したものとみなし、当社は、第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">14</span>
                        <span style="font-family:宋体; font-size:12pt">条の定めに従い、加盟店に当該テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品に係る売上金を支払うものとする。なお、その場合、加盟店が加盟店の判断で当該テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品を処分するものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">13</span>
                        <span style="font-family:宋体; font-size:12pt">条（注文の確認）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は加盟店専用の管理画面を表示できる端末を用意し、該当端末に</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">ID</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">パスワードでログインする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は加盟店専用の管理画面を表示できる当社指定のプリンタを用意し、該当プリンタに</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">ID</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">パスワードでログインする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は注文が常時確認できる状態に前項の端末をセッティングするものとする。注文の見落とし、テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の内容の誤り等は加盟店の責任とし、当社は一切の責任を負わないものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">14</span>
                        <span style="font-family:宋体; font-size:12pt">条（売上金の代理受領）</span>
                        <br />
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、当社に対し、加盟店とユーザーとの間におけるテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の売買契約の代金を加盟店に代わって受領する権限を付与し、当社は加盟店に代わって当該代金をユーザーから受領するものとする。なお、</span>
                        <span style="font-family:宋体; font-size:12pt">株式会社</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">QbayTechnology</span>
                        <span style="font-family:宋体; font-size:12pt">が決済業務を受託する。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">15</span>
                        <span style="font-family:宋体; font-size:12pt">条（売上金の支払）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、加盟店の当システムにおけるテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の売上金から決済手数料と利用手数料と振込手数料を差し引いた金額を、当社が別途定める期日に、加盟店の届け出た金融機関に振り込むものとする。ただし、当社は、加盟店の当システムにおける売上金の総額から決済手数料と利用手数料を差し引いた金額が加盟店設定した金額を超えるまでの期間、加盟店に対する売上金の支払いを留保することができるものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店の届け出た振込先情報に誤りがあった場合、当社はその旨を加盟店に通知し、加盟店はすみやかに振込先情報を修正するものとする。その場合、当社は、金融機関情報の修正を受け付けたタイミングから起算して次回の振込日に、当初振込金額から差戻手数料と振込手数料を再度差し引いて支払うものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">16</span>
                        <span style="font-family:宋体; font-size:12pt">条（営業の確約）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、加盟店管理画面において自ら定めた営業日及び営業時間に従って当システムを利用し、ユーザーがテーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span style="font-family:宋体; font-size:12pt">配達商品を購入可能な状態を維持しなければならない。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span style="font-family:宋体; font-size:12pt">前項に違反する事実が</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">3</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >営業日（加盟店が加盟店管理画面に定めた営業日を基準とする）連続した場合、当社は、当該加盟店の営業状況及び当システム、端末の状態の確認を求めることがある。この場合、加盟店は、当社の求めに応じて速やかに状況を報告するとともに、当システムの利用に関して当社からの指導があった場合にはこれに従うものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">17</span>
                        <span style="font-family:宋体; font-size:12pt">条（法令遵守）</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">加盟店は、テーブルオーダー、テイクアウト</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">/</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >配達商品の提供その他当システムの利用に関して、食品衛生法及び食品表示法その他適用される法令等を遵守するものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">18</span>
                        <span style="font-family:宋体; font-size:12pt">条（禁止事項）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span style="font-family:宋体; font-size:12pt">加盟店は、以下の行為を行ってはならない。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">①</span>
                        <span style="font-family:宋体; font-size:12pt">法令の定めに違反する行為又はそのおそれのある行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">②</span>
                        <span style="font-family:宋体; font-size:12pt">公序良俗に反する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">③</span>
                        <span style="font-family:宋体; font-size:12pt">日本通信販売協会が定める広告に関する自主基準に違反する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">④</span>
                        <span style="font-family:宋体; font-size:12pt">ユーザー又は第三者の判断に誤解を与えるおそれのある行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑤</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社、他の加盟店、ユーザー又は第三者に対し、財産権（知的財産権を含む）の侵害、名誉</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >プライバシーの侵害、誹謗中傷、その他の不利益を与える行為又はそのおそれのある行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑥</span>
                        <span style="font-family:宋体; font-size:12pt">他のシステム内店舗の宣伝、外部</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">Web</span>
                        <span style="font-family:宋体; font-size:12pt">サイトへのハイパーリンク、電話</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">FAX</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >電子メールなどを利用したサイト外取引についての優遇措置の表示、その他の方法によりユーザーを当システム外の取引に誘引する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑦</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >ユーザー又は第三者の個人情報、登録情報、利用履歴情報などを、不正に収集、開示又は提供する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑧</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当システムの利用を通じて取得した電子メールアドレスに対し、当システムを介さない方法により広告</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">宣伝を内容とする電子メールを配信する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑨</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >本規約終了後に、当システムの加盟店管理画面の運営に関連し取得したメールアドレスその他のユーザー情報を利用する行為（広告</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >宣伝を内容とする電子メールの配信その他の勧誘を含むが、これに限られない）</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑩</span>
                        <span style="font-family:宋体; font-size:12pt">当システムと同種又は類似のサービスの提供</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑪</span>
                        <span style="font-family:宋体; font-size:12pt">当社のサービス業務の運営</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">維持を妨げる行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑫</span>
                        <span style="font-family:宋体; font-size:12pt">当システムに関して利用し得る情報を改ざんする行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑬</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >有害なコンピュータプログラム、メール等を当システムに送信又は書き込む行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑭</span>
                        <span style="font-family:宋体; font-size:12pt">サーバその他当社のコンピュータに不正にアクセスする行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑮</span>
                        <span style="font-family:宋体; font-size:12pt">当社が別途禁止行為として定める行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑯</span>
                        <span style="font-family:宋体; font-size:12pt">本規約に違反する行為</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、法令により販売が禁止されている商品等、第三者の権利を侵害するおそれのある商品等、当社が別途販売禁止として加盟店に通知した商品等又は当システムのイメージに合致しないと当社が判断した商品等の販売をすることができない。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">19</span>
                        <span style="font-family:宋体; font-size:12pt">条（利用停止</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">解除）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、加盟店が以下のいずれかに該当し、又は該当するおそれがあると当社が判断した場合、加盟店による当システムの利用を予告なく一時停止し、又は何らの催告を要することなく加盟店との間の本規約を解除して、当システムの提供を中止することができるものとする。かかる利用停止又は解除に起因して加盟店に何らかの損害が生じた場合であっても、当社は、加盟店に対し、何ら責任を負わないものとする。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">①</span>
                        <span style="font-family:宋体; font-size:12pt">当システム利用開始後に当社が第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">3</span>
                        <span style="font-family:宋体; font-size:12pt">条第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">2</span>
                        <span style="font-family:宋体; font-size:12pt">項に定める申込拒否事由があることを知った場合</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">②</span>
                        <span style="font-family:宋体; font-size:12pt">本規約に違反した場合</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">③</span>
                        <span style="font-family:宋体; font-size:12pt">不正又は不当な目的をもって当システムを利用した場合</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">④</span>
                        <span style="font-family:宋体; font-size:12pt">当社の責に帰すべき理由によらず、加盟店と連絡が取れない場合</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑤</span>
                        <span style="font-family:宋体; font-size:12pt">当システムのアカウントへ</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">1</span>
                        <span style="font-family:宋体; font-size:12pt">年以上アクセスしていない場合</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑥</span>
                        <span style="font-family:宋体; font-size:12pt">その他、当社が不適切と判断する行為を行った場合</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span style="font-family:宋体; font-size:12pt">前項の定めに関わらず当社は加盟店に対して</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">14</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >日前に通知することにより加盟店の利用を停止又は本規約を解除することができるものとする。かかる利用停止又は解除に起因して加盟店に何らかの損害が生じた場合であっても、当社は、加盟店に対し、何ら責任を負わないものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">20</span>
                        <span style="font-family:宋体; font-size:12pt">条（非保証</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">免責）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、当システムに事実上又は法律上の瑕疵（安全性、信頼性、正確性、完全性、有効性、特定の目的への適合性、セキュリティなどに関する欠陥、エラーやバグ、権利侵害等を含むが、これらに限らない）がないことを明示的にも黙示的にも保証しない。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、加盟店が当システムを利用したことに起因して加盟店に生じたあらゆる損害について一切の責任を負わない。ただし、この損害が当社の故意又は重大な過失によって発生した場合はこの限りでなく、この場合、当社は加盟店に生じた通常かつ直接の損害について、この損害が発生した月に加盟店が現に支払った利用手数料を上限として、これを賠償する責任を負うものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">21</span>
                        <span style="font-family:宋体; font-size:12pt">条（機密保持）</span>
                        <br />
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、当社の機密情報（当社の顧客、製品、サービス、事業、技術、ノウハウ、アイディア、コンセプト等に関する一切の情報であって、その開示方法にかかわらず、当社が開示の際に秘密である旨を明示したものをいう）を秘密として保持するものとし、法令により開示が義務付けられる場合を除き、当社の書面による事前の承諾なく当該機密情報を第三者に開示又は漏洩してはならないものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">22</span>
                        <span style="font-family:宋体; font-size:12pt">条（反社会的勢力の排除）</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">1.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、自己又はその代表者、役員、実質的に経営権を有する者、従業員又は代理人（以下「関係者」という）が、現在、暴力団、暴力団員、暴力団員でなくなった時から</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">5</span>
                        <span style="font-family:宋体; font-size:12pt">年を経過しない者、暴力団準構成員、暴力</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >団関係企業、総会屋等、社会運動等標ぼうゴロ、特殊知能暴力集団等又はこれらの者と密接な関わりを有する者若しくはこれらに準じる者（以下「反社会的勢力」という）のいずれにも該当しないことを表明し、かつ、将来にわたっても該当しないことを確約するものとする。と密接な関わりを有する者若しくはこれらに準じる者（以下「反社会的勢力」という）のいずれにも該当しないことを表明し、かつ、将来にわたっても該当しないことを確約するものとする。</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">2.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店は、自ら又はその関係者が、直接的又は間接的に、以下の行為を行わないことを確約するものとする。</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">①</span>
                        <span style="font-family:宋体; font-size:12pt">暴力的な要求行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">②</span>
                        <span style="font-family:宋体; font-size:12pt">法的な責任を超えた不当な要求行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">③</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >取引に関して、脅迫的な言動（自己又はその関係者が反社会的勢力である旨を伝えることを含むが、これに限らない）をし、又は暴力を用いる行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">④</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >風説を流布し、偽計を用い又は威力を用いて当社の信用を毀損し、又は当社の業務を妨害する行為</span>
                        <br />
                        <span style="font-family:'Cambria Math'; font-size:12pt">⑤</span>
                        <span style="font-family:宋体; font-size:12pt">その他前各号に準じる行為</span>
                        <br />
                        <span style="font-family:'Avenir Next'; font-size:12pt">3.</span>
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >加盟店が前二項に定める表明事項又は確約事項のいずれかに違反することが判明した場合、当社は、何らの催告を要することなく本規約を解除して、当システムの提供を中止することができるものとする。かかる解除に起因して加盟店に何らかの損害が生じた場合であっても、当社は、加盟店に対し、何ら責任を負わないものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">23</span>
                        <span style="font-family:宋体; font-size:12pt">条（本規約の変更）</span>
                        <br />
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >当社は、必要に応じ、公表又は加盟店に通知する方法により、本規約を変更できるものとする。加盟店は、本規約の変更後に引き続き当システムを利用した場合、本規約の変更に同意したものとみなす。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">24</span>
                        <span style="font-family:宋体; font-size:12pt">条（協議解決）</span>
                        <br />
                        <span
                            style="font-family:宋体; font-size:12pt"
                        >本規約に定めのない事項又は本規約の内容について疑義が生じた場合は、当社と加盟店は誠意を持って協議解決に当たるものとする。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:justify">
                        <span style="font-family:宋体; font-size:12pt">第</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">25</span>
                        <span style="font-family:宋体; font-size:12pt">条</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">(</span>
                        <span style="font-family:宋体; font-size:12pt">準拠法及び合意管轄</span>
                        <span style="font-family:'Avenir Next'; font-size:12pt">)</span>
                        <br />
                        <span style="font-family:宋体; font-size:12pt">本規約の解釈</span>
                        <span style="font-family:微软雅黑; font-size:12pt">・</span>
                        <span style="font-family:宋体; font-size:12pt">適用は、日本国法に準拠し、本規約に係る全ての紛争については、</span>
                        <span style="font-family:宋体; font-size:12pt">東京地方裁判所を第一審の専属管轄裁判所とします。</span>
                    </p>
                    <p style="background-color:#ffffff; text-align:right">
                        <span style="font-family:宋体; font-size:12pt">以上</span>
                    </p>
                    <p style="margin:0pt; orphans:0; text-align:justify; widows:0">
                        <span style="font-family:等线; font-size:10.5pt">&nbsp;</span>
                    </p>
                </div>
            </div>
            <div v-if="index==2">
                <div>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span class="title">個人情報保護方針</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span style="font-family:'.PingFang SC'; font-size:12pt">当社は、</span>
                        <span style="font-family:AppleSystemUIFont; font-size:12pt">WEB</span>
                        <span style="font-family:'.PingFang SC'; font-size:12pt">サービス事業、</span>
                        <span style="font-family:AppleSystemUIFont; font-size:12pt">WEB</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >制作事業、人材紹介派遣事業を通じて社会に貢献することを使命とし、サービス品質の向上、お客様満足度の向上に向け努力しております。また、コンプライアンス重視、法令遵守は当然のこと、当社が個人情報を保護する事は、欠かすことの出来ない社会的責務であると考えてお</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >ります。そのため、全従業者に個人情報保護の重要性を認識させ、個人情報が常に適切に取扱われるよう周知徹底しております。当社の個人情報保護に対する取り組みの基本姿勢を、以下の通り宣言いたします。</span>
                    </p>
                    <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
                        <span
                            style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
                        >1</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
                        >．個人情報の取得·利用·提供</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >当社は取得する個人情報の利用目的を特定し、その利用目的を達成するために必要な範囲でのみ個人情報を取得します。</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >当社は、個人情報を直接ご本人様から取得する場合、その利用目的や取扱いについて、文書あるいはそれに代わる方法でご同意いただいております。（お客様のみならず、従業員、取引先様も含めて、当社が事業の用に供する全ての個人情報を対象とします。）</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >また、次の場合をのぞき、利用目的の達成に必要な範囲を超えた取扱い（以下「目的外利用」）及び、本人の承諾を得ずに第三者に開示·提供することはいたしません。これらのことを防止する措置を、当社の個人情報保護マネジメントシステムによって確実に実施します。</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span style="font-family:'.PingFang SC'; font-size:12pt">（ａ）法令に基づく場合</span>
                        <span style="font-family:'MS Gothic'; font-size:12pt"></span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >（ｂ）人の生命、身体又は財産の保護のために必要がある場合であって、本人の同意を得ることが困難であるとき</span>
                        <span style="font-family:'MS Gothic'; font-size:12pt"></span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >（ｃ）公衆衛生の向上又は児童の健全な育成の推進のために特に必要がある場合であって、本人の同意を得ることが困難であるとき</span>
                        <span style="font-family:'MS Gothic'; font-size:12pt"></span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >（ｄ）国の機関もしくは地方公共団体又はその委託を受けた者が法令の定める事務を遂行することに対して協力する必要がある場合であって、本人の同意を得ることによって当該事務の遂行に支障を及ぼすおそれがあるとき</span>
                    </p>
                    <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
                        <span
                            style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
                        >2.</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
                        >法令、国が定める指針その他規範の遵守</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >当社は、個人情報に関する法令、国が定める指針その他規範を遵守いたします。</span>
                    </p>
                    <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
                        <span
                            style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
                        >3.</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
                        >個人情報の漏えい、滅失又はき損の防止及び是正について</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >当社は、個人情報の漏えい、滅失又はき損の防止及び是正に関して、必要かつ適</span>
                        <span style="font-family:'.PingFang SC'; font-size:12pt">切な安全対策を実施いたします。</span>
                    </p>
                    <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
                        <span
                            style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
                        >4.</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
                        >個人情報に関する苦情および相談について</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >個人情報の取扱いに関する苦情及び相談については、迅速かつ適切に対応いたします。下記の個人情報に関するお問い合わせ窓口にご連絡下さい。</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >また、当社は認定個人情報保護団体「一般財団法人日本情報経済社会推進協会」の対象事業者であるため、下記の「一般財団法人日本情報経済社会推進協会」の問合せ窓口へお申し出頂くことも可能でございます。</span>
                    </p>
                    <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
                        <span
                            style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
                        >5.</span>
                        <span
                            style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
                        >個人情報保護マネジメントシステムの継続的改善</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >当社は、個人情報の保護と、適切な取扱いについて、行動規範、内部規程、ルールを定めた、個人情報保護マネジメントシステムを構築し、運用します。また、定期的に実施する運用の点検、内部監査、経営による見直しを通して、個人情報保護マネジメントシステムを継続的に改善いたします。</span>
                    </p>
                    <p style="margin:0pt; orphans:0; widows:0">
                        <span
                            style="font-family:'.PingFang SC'; font-size:12pt"
                        >本方針を全従業者に周知徹底させるとともに、全ての関係者様に公表いたします。</span>
                    </p>
                    <p style="margin:0pt; orphans:0; text-align:justify; widows:0">
                        <span style="font-family:等线; font-size:10.5pt">&nbsp;</span>
                    </p>
                </div>
            </div>
